@import url(https://db.onlinewebfonts.com/c/0c3856a335fdfdb8b0652f7c3d367fdd?family=Moderat);

html {
  font-family: Moderat;
  font-size: 15px;
}

*,
*:before,
*:after {
  box-sizing: inherit;
  margin-block-start: 0;
  margin-block-end: 0;
  outline:none;
}

.container {
  margin-left: 20%;
  width: 60%;
  @media only screen and (max-width: 1500px) and (min-width: 1024px){
    margin-left: 15%;
    width: 70%;
  }
  @media only screen and (max-width: 1024px) and (min-width: 769px){
    margin-left: 10%;
    width: 80%;
  }
  @media only screen and (max-width: 769px){
    margin-left: 0%;
    width: 100%;
  }
}

.sub-container {
  margin-left: 20%;
  width: 60%;
  @media only screen and (max-width: 1500px) and (min-width: 1024px){
    margin-left: 15%;
    width: 70%;
  }
  @media only screen and (max-width: 1024px) and (min-width: 769px){
    margin-left: 10%;
    width: 80%;
  }
  @media only screen and (max-width: 769px){
    margin-left: 5%;
    width: 90%;
  }
}

.text-container {
  width: 50%;
  @media only screen and (max-width: 1500px) and (min-width: 1024px){
    margin-left: 0%;
    width: 60%;
  }
  @media only screen and (max-width: 1024px) and (min-width: 769px){
    margin-left: 2.5%;
    width: 75%;
  }
  @media only screen and (max-width: 769px){
    margin-left: 1%;
    width: 90%;
  }
}
.hide-on-mobile {
  @media only screen and (max-width: 769px){
    display: none;
  }
}


body {
  margin: 0;
  font-family: inherit;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  height: 100%;
  
  h1 {
    font-weight: 500;
    font-size: 2rem;
    color: white;
    line-height: 2.625rem;
    letter-spacing: 0.8px;
  }
  
  h2 {
    font-weight: 600;
    font-size: 1.2rem;
    color: #121215;
    line-height: 1.6875rem;
    letter-spacing: 0px;
  }
  
  p {
  }
  
  code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
      monospace;
  }

  
  a, a:visited {
    text-decoration: none;
    color: inherit;
  }
}

