.navbar {
  z-index: 100;
  max-width: 100%;
  box-sizing: border-box;
  color: black;
  position: sticky;
  top: 0;
  margin-top: 1rem;
  height: 3rem;
  background: #FFF;
  nav {
    margin-bottom: 1rem;
    height: 3rem;
    border-bottom: 1px solid #F2F2F2;
    display: flex;
    justify-content: space-between;
    align-items: center;
    .left {
      display: flex;
      justify-content: space-between;
      font-weight: 500;
      a, div {
        margin-right: 2vw;
        width: auto;
      }
      @media only screen and (max-width: 769px){
        margin-left: 1%;
      }
    }
    .right {
      font-weight: bold;
      @media only screen and (max-width: 769px){
        display: none;
      }
      font-size: 1rem;
      color: #3873F9;
      display: flex;
      justify-content: flex-end;
    }    
  }
  a, a:visited {
    color: inherit;
    text-decoration: none;
  }
}